@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	text-decoration: none;
	list-style: none;
}

:root {
	--color-orange: #ff9900;
	--color-white: #fff;
	--color-grey-lt: #f1f1eb;
	--color-grey-md: #e5e5df;
	--header-padding: 2em;
}

body {
	color: #333;
	background-color: #e5e5df;
	background-color: var(--color-grey-md);
	font-family: 'Ubuntu', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-y: hidden;
}

/* =================================
    shared classes across components
   =================================       
 */

.container {
	width: 95%;
	max-width: 1100px;
	margin: 0 auto;
}

.center {
	text-align: center;
}

.flex {
	display: flex;
}

.jokes__loader {
	width: 40vw;
	text-align: center;
}

@media only screen and (max-width: 600px) {
	.jokes__loader {
		display: grid;
		place-items: center;
		text-align: unset;
	}
}

/* ================================
    header styles
   =================================       
 */

.header {
	color: var(--color-white);
	background-color: var(--color-orange);
    padding: var(--header-padding);
}

.header .center {
	text-align: center;
}

.header > div {
    justify-content: space-between;
}

.header__logo {
	color: inherit;
}

.header__logo sup {
	font-size: 0.6rem;
	font-weight: bold;
}


@media only screen and (max-width:600px) {
	.header > div {
		flex-direction: column;
		align-items: center;
	}
	.header__logo {
		margin-bottom: 1em;
	}
	.nav__link {
		font-size: 0.9rem;
	}
}

